import React, { ReactNode } from 'react';
import { Container, makeStyles } from '@material-ui/core';
import { useSpring, animated } from 'react-spring';
import Header from 'components/Header';
import Footer from 'components/Footer';
import SEO from 'components/Utils/SEO';

const useStyles = makeStyles(({ palette }) => ({
  container: {
    marginTop: '5rem',
    marginBottom: '3rem',
  },
  title: {
    color: palette.text.primary,
  },
  description: {
    color: palette.text.secondary,
    whiteSpace: 'pre-line',
  },
  innterContainer: {
    backgroundColor: palette.common.white,
  },
}));

interface Props {
  children: ReactNode;
  pageName?: string;
  showTitle?: boolean;
  description?: ReactNode;
  hideFooter?: boolean;
}
const Page = ({ children, pageName, showTitle = false, description, hideFooter = false }: Props) => {
  const styles = useStyles();
  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(-30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });

  return (
    <div data-layout="page">
      <SEO title={pageName} />
      <Header />
      <div>
        {showTitle ? (
          <Container className={styles.container}>
            <animated.h1 style={titleAnimation} className={styles.title}>
              {pageName}
            </animated.h1>
            {description && (
              <animated.p style={descriptionAnimation} className={styles.description}>
                {description}
              </animated.p>
            )}
          </Container>
        ) : null}
        <div className={styles.innterContainer}>{children}</div>
      </div>
      {!hideFooter && <Footer />}
    </div>
  );
};

export default Page;
