import React from 'react';
import { Link } from 'gatsby';
import { Grid, makeStyles, useMediaQuery, useTheme } from '@material-ui/core';
import { GlobalMenus } from 'constants/menu';
import RouteLink from 'components/Utils/RouteLink';
import { getLogoSrc } from 'utils/logo';
import { logger } from '@lubycon/logger';

const useStyles = makeStyles(({ spacing, breakpoints, palette }) => ({
  root: {
    position: 'fixed',
    width: '100%',
    padding: spacing(1),
    top: 0,
    left: 0,
    zIndex: 10,
    backdropFilter: 'blur(4px)',
  },
  menus: {
    display: 'flex',
    padding: 0,
    margin: 0,
    alignItems: 'center',
    height: '100%',
  },
  menuItems: {
    fontSize: '1rem',
    listStyle: 'none',
    marginRight: spacing(2),
    [breakpoints.down('md')]: {
      fontSize: '0.8rem',
      marginRight: spacing(1),
    },
  },
  logo: {
    width: 130,
    [breakpoints.down('md')]: {
      width: 40,
    },
  },
  link: {
    color: palette.text.secondary,
    textDecoration: 'none',
    transition: 'color 0.2s ease-in',
    '&:hover': {
      color: palette.primary.main,
    },
    [breakpoints.down('md')]: {
      fontSize: '0.7rem',
    },
  },
}));

const headerLogger = logger.getPageLogger('header_global_nav');

const Header = () => {
  const styles = useStyles();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down('md'));
  const logoSrc = getLogoSrc(matches ? 'symbol' : 'logo');

  return (
    <header className={styles.root}>
      <Grid justify="space-between" alignItems="center" container>
        <Grid xs="auto" item>
          <Link to="/" onClick={() => headerLogger.click('logo')}>
            <img className={styles.logo} src={logoSrc} alt=" " />
          </Link>
        </Grid>
        <Grid xs="auto" item>
          <ul className={styles.menus}>
            {GlobalMenus.map((menu, index) => (
              <li key={index} className={styles.menuItems}>
                <RouteLink
                  to={menu.link}
                  className={styles.link}
                  onClick={() => headerLogger.click('header_menu', { name: menu.name, link: menu.link })}
                >
                  {menu.name}
                </RouteLink>
              </li>
            ))}
          </ul>
        </Grid>
      </Grid>
    </header>
  );
};

export default Header;
